import React from "react";
import Table from "react-bootstrap/Table";
import CustomSelect from "../Select/customSelect";
import "./staticTable.css";

const StaticTable = ({
  columnHeaders, // from Excel column Headers//
  handleSelectChange,
  selectedMappings,
  mappedAttributes,
  mandatoryAttributes,
}) => {
  const { allNLSValues = [], dropdownOptions = [] } = mappedAttributes || {};

  console.log("Mapped Attributes prop:", mappedAttributes);
  console.log("columnHeaders", columnHeaders);
  console.log("static table recieved manditory Attribute", mandatoryAttributes);
  console.log("Dropdown Options :", dropdownOptions);
  console.log("Selected Mappings:", selectedMappings);
  console.log("All NLS Values:", allNLSValues);

  // Checks if column header matches any NLS value
  const hasMatchingNLS = (header) => {
    if (!Array.isArray(allNLSValues)) {
      console.warn("allNLSValues is not an array:", allNLSValues);
      return false;
    }

    // Simple case-insensitive exact matching
    const normalizedHeader = header.toLowerCase().trim();
    const hasMatch = allNLSValues.some(
      (nls) => nls.toLowerCase().trim() === normalizedHeader
    );

    if (allNLSValues.length > 0) {
      // Only log after API hit
      console.log(
        `Column "${header}" NLS match:`,
        hasMatch ? "Found ‚úÖ" : "Not Found ‚ùå"
      );
    }
    return hasMatch;
  };

  // Gets backend name for selected UI label
  const getBackendNameForUILabel = (uiLabel) => {
    const option = dropdownOptions.find((opt) => opt.uiLabel === uiLabel);
    return option ? option.backendName : uiLabel;
  };

  return (
    <Table bordered className="table-light ">
      <thead>
        <tr>
          <th className="header-styling"> Column Name</th>
          <th className="header-styling">Attribute Name</th>
        </tr>
      </thead>
      <tbody>
        {columnHeaders.map((header, index) => {
          const isMandatory = mandatoryAttributes.includes(header);
          const hasNLS = hasMatchingNLS(header);
          const currentMapping = selectedMappings[header];

          return (
            <tr key={index}>
              <td>{header}</td>
              <td>
                <CustomSelect
                  index={index}
                  // Use uiLabel property for the dropdown display; falls back to header if not set.
                  selectedValue={currentMapping?.uiLabel || header}
                  onChange={(index, value) => {
                    // Get backend name for the selected UI label.
                    const backendName = getBackendNameForUILabel(value);
                    // Pass an object containing both UI label and backend name.
                    handleSelectChange(header, { uiLabel: value, backendName });
                  }}
                  size="lg"
                  className="w-100"
                  disabled={!hasNLS}
                  options={{
                    defaultLabel: hasNLS
                      ? header
                      : "Please select from Drop Down",
                    list: dropdownOptions.map((attr) => ({
                      value: attr.uiLabel, // Show UI label in dropdown
                      label: attr.uiLabel,
                    })),
                  }}
                />
              </td>
            </tr>
          );
        })}
      </tbody>
    </Table>
  );
};

export default StaticTable;
